<template>
  <div>
    <div v-show="showLoading">
      <Loading></Loading>
    </div>
    <div
      class="position-absolute background"
      v-show="!showLoading"
    >
      <b-container>
        <Header
          dark
          title="Desejamos as boas vindas"
        />
        <!-- Slider main container -->
        <div class="swiper-container">
          <!-- Additional required wrapper -->
          <div class="swiper-wrapper">
            <!-- Slides -->
            <div
              v-for="content in contents"
              :key="content.text"
              class="swiper-slide d-flex justify-content-center flex-column"
            >
              <div class="card mx-4">
                <small class="text-center text-uppercase my-2">O que ?</small>
                <h5 class="text-center font-weight-bolder text-uppercase txt-orange mx-3 mx-sm-0">
                  {{ nameQuiz }}
                </h5>
                <WelcomeBody>
                  <span v-html="content.bodyText"></span>
                </WelcomeBody>
                <p class="text-center px-5 f-12 mt-4">
                  {{content.text}}
                </p>
              </div>
            </div>
          </div>
          <!-- If we need pagination -->
          <div class="swiper-pagination position-relative"></div>
        </div>
        <div class="py-4">
          <Button
            @click="showLoadingPage"
            class="text-center"
            id="formSubmit"
            color="success"
            label="Iniciar avaliao"
            iconClass="icon-arrow-right"
          />
        </div>
      </b-container>
    </div>
  </div>

</template>

<script>
import Button from '../components/Button'
import Header from '../components/Header'
import Swiper from 'swiper'
import Loading from '../components/Loading'
import WelcomeBody from '../components/welcome/WelcomeBody'
import { mapGetters } from 'vuex'

export default {
  components: {
    Button,
    Header,
    Loading,
    WelcomeBody
  },
  data () {
    return {
      showLoading: false,
      contents: [
        {
          bodyText: `
            <p> Nossas boas vindas ao <strong>QualiQuiz</strong>, o teste de mltipla escolha que vai mensurar seus conhecimentos no campo da Sade. </p>
            <p> O <strong>QualiQuiz</strong>  uma criao da <strong>Escola de Sade Pblica do Cear</strong> para promover avaliaes por competncias dos profissionais de sade que atuam na rede do SUS. </p>
            <p> Ao responder esse quiz, voc nos ajuda a conhecer melhor suas potencialidades e fragilidades, e a qualificar nossa poltica de educao permanente, com base em um planejamento educacional adequado s necessidades do SUS em nosso estado.</p>
            <p> Para comear seu teste clique no boto abaixo. </p>
          `,
          text: ' uma ferramsenta de avaliao longitudinal, de estudantes e de programas educacionais.',
          img: require('../assets/images/blank.png')
        }
      ]
    }
  },
  computed: {
    ...mapGetters('quiz', {
      nameQuiz: 'getName'
    })
  },
  methods: {
    showLoadingPage () {
      this.showLoading = true
      setTimeout(() => {
        this.$router.push({ name: 'Question' })
      }, 3000)
    }
  },
  mounted () {
    // eslint-disable-next-line no-new
    new Swiper('.swiper-container', {
      // If we need pagination
      pagination: {
        clickable: true,
        el: '.swiper-pagination'
      },
      spaceBetween: -35
    })
  }
}
</script>

<style
  lang="scss"
  scoped
>
  small {
    font-family: 'OpenSans-Bold';
  }

  .swiper-container {
    width: 100%;
  }

  .txt-orange {
    color: $burning-orange;
  }

  .swiper-pagination {
    bottom: 0 !important;
  }

  .swiper-pagination-bullet
  .swiper-pagination-bullet-active {
    margin: 5px
  }

  .swiper-pagination ::v-deep
  .swiper-pagination-bullet-active {
    background-color: $burning-orange !important;
  }
</style>
